.EndScreen {
    top: 56px;
    position: absolute;
    display: block;
    width: 100%;
    margin: auto;
    height: 100%;
    overflow: scroll;
    text-align: center;
}

.RestartButton {
    background-color: green;
    color: white;
    font-size: 1.2rem;
    padding: 5px;
    width: 150px;
    border: 1px solid green;
    cursor: pointer;
    outline: none;
    left: 50%;
    margin-top: 20px;
}

.RestartButton:hover {
    background-color: aquamarine;
    color: white;
}

.Results {
    display: inline;
    color: green;
}

.ShareText {
    text-align: center;
    margin-top: 50px;
    font-family: 'Open Sans', sans-serif;
    font-size: 1.2rem;
    font-weight: normal;
}

.Stats {
    text-align: center;
    margin-top: 30px;
    font-family: 'Open Sans', sans-serif;
    font-size: 0.8rem;
    font-weight: lighter;
}

.Stats p {
    margin: 0px
}

.ShareButtons {
    text-align: center;
    margin-top: 20px;
}

.ShareButton {
    padding: 5px;
}